.footer {
	background: #c9d6ff; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to right,
		#e2e2e2,
		#c9d6ff
	); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#e2e2e2,
		#c9d6ff
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

	color: black;
	height: auto;
}

.footerContainer {
	height: auto;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	padding: 100px;
	padding-top: 80px;
}

.email {
	display: flex;
	flex-direction: column;
}

.title {
	font-size: 28px;
	color: rgba(0, 0, 0, 0.65);
	font-weight: bold;
	margin-bottom: 20px;
}

.emailTo > span {
	color: rgba(0, 0, 0, 0.55);
	font-size: 16px;
}

.emailTo span:first-child {
	margin-right: 10px;
}

.mailAddress > a {
	text-decoration: none;
	color: rgba(0, 0, 0, 0.55);
	font-size: 16px;
}

.pageContainer,
.supportContainer {
	display: flex;
	flex-direction: column;
}

.pageContainer > span,
.supportContainer > span {
	margin-bottom: 20px;
	cursor: pointer;
}

.link {
	text-decoration: none;
	color: rgba(0, 0, 0, 0.55);
	font-size: 16px;
}

.underlineEffect > a {
	text-decoration: none;
	color: rgba(0, 0, 0, 0.55);
}

.underlineEffect {
	background: linear-gradient(to right, #fff, #eee) no-repeat right bottom;
	cursor: pointer;
	background-size: 0 2px;
	transition: background-size 0.6s;
	margin-left: 10px;
}

.underlineEffect:hover {
	background-position-x: left;
	background-size: 100% 2px;
}
