.project {
	background: #c9d6ff; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to right,
		#e2e2e2,
		#c9d6ff
	); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#e2e2e2,
		#c9d6ff
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.projectPage {
	height: 240px;
}

.font {
	margin-left: 200px;
	position: absolute;
	top: 15%;
	font-size: 42px;
	color: #fff;
}

hr {
	border: none;
	border-top: 3px solid rgba(255, 255, 255, 0.55);
	color: #333;
	overflow: visible;
	text-align: left;
	height: 5px;
	width: 600px;
	margin-left: 200px;
}

.projectContainer {
	display: flex;
	justify-content: center;
	flex-direction: column;
	margin-left: 200px;
	gap: 20px;
}

.projectContainer > span {
	margin-top: 60px;
	color: rgba(0, 0, 0, 0.55);
	font-size: 36px;
}
