.Navbar {
	height: 80px;
	width: 100%;
	background: #c9d6ff; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to right,
		#e2e2e2,
		#c9d6ff
	); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#e2e2e2,
		#c9d6ff
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-direction: row;
	/* border-bottom: 1px solid white; */
	position: fixed;
	z-index: 999;
}

.Navbar::after {
	content: '';
	position: absolute;
	bottom: 0;
	left: 150px;
	width: 80%;
	height: 3px;
	background-color: #fff;
	opacity: 0.4;
}

.active {
	color: black;
}

.logo {
	margin-left: 200px;
	margin-bottom: 6px;
	color: #fff;
	font-size: 40px;
	font-weight: bold;
}

.selectorGroup {
	margin-right: 200px;
}

.selectorGroup > span {
	margin: 0 20px;
	color: white;
	font-size: 20px;
	font-weight: bold;
}

.underlineEffect {
	background: linear-gradient(to right, #fff, #eee) no-repeat right bottom;
	cursor: pointer;
	background-size: 0 2px;
	transition: background-size 0.6s;
}

.underlineEffect:hover {
	background-position-x: left;
	background-size: 100% 2px;
}
