.ProjectCard {
	height: 300px;
	border: 1px solid transparent;
	width: 80%;
	padding: 20px;
	border-radius: 20px;
	transition: 0.6s;
	position: relative;
}

.ProjectCard:hover {
	transform: scale(1.002);
	box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.3),
		0 1px 3px 1px rgba(60, 64, 67, 0.15);
}

.finishTime {
	display: flex;
	gap: 10px;
	align-items: flex-end;
	font-size: 24px;
	margin-top: 20px;
	margin-left: 50px;
	margin-bottom: 30px;
	color: rgba(0, 0, 0, 0.65);
}

.year,
.month,
.day {
	font-size: 26px;
}

.projectTitle {
	font-size: 36px;
	margin-left: 50px;
	color: rgba(0, 0, 0, 0.65);
}

.projectDesc {
	margin-left: 50px;
	color: rgba(0, 0, 0, 0.45);
}

.projectLink {
	margin-top: 40px;
	font-size: 18px;
	margin-left: 50px;
	width: 200px;
}

.projectLink a {
	text-decoration: none;
	color: rgba(0, 0, 0, 0.55);
}

.dependency {
	position: absolute;
	right: 80px;
	bottom: 20px;
}

.dependency > img {
	width: 50px;
}

.underlineEffect {
	background: linear-gradient(
			to right,
			rgba(0, 0, 0, 0.55),
			rgba(0, 0, 0, 0.55)
		)
		no-repeat right bottom;
	cursor: pointer;
	background-size: 0 1px;
	transition: background-size 0.6s;
}

.underlineEffect:hover {
	background-position-x: left;
	background-size: 100% 1px;
}
