.skill {
	background: #c9d6ff; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to right,
		#e2e2e2,
		#c9d6ff
	); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#e2e2e2,
		#c9d6ff
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	border-bottom: 2px solid rgba(255, 255, 255, 0.3);
}

.skillPage {
	height: 240px;
}

.font {
	margin-left: 200px;
	position: absolute;
	top: 15%;
	font-size: 42px;
	color: #fff;
}

.Learned {
	margin-left: 200px;
	font-size: 42px;
	color: #fff;
}

hr {
	border: none;
	border-top: 3px solid rgba(255, 255, 255, 0.55);
	color: #333;
	overflow: visible;
	text-align: left;
	height: 5px;
	width: 600px;
	margin-left: 200px;
}

.LearnedContianer {
	width: 73%;
	background-color: transparent;
	margin-left: 200px;
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
	gap: 10px;
	margin-bottom: 80px;
}

.LearningContianer {
	width: 73%;
	background-color: transparent;
	margin-left: 200px;
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
	gap: 10px;
	margin-bottom: 160px;
}

.LearningContianer > span {
	margin-top: 60px;
	color: rgba(255, 255, 255, 0.55);
