:root {
	--title-color: rgba(0, 0, 0, 0.55);
}

::-webkit-scrollbar {
	display: none;
}

* {
	user-select: none;
	font-family: 'sohne-var', 'Helvetica Neue', 'Arial', sans-serif;
}

.about {
	background: #c9d6ff; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to right,
		#e2e2e2,
		#c9d6ff
	); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#e2e2e2,
		#c9d6ff
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	border-bottom: 2px solid rgba(255, 255, 255, 0.3);
}

.aboutPage {
	height: 240px;
}

.font {
	margin-left: 200px;
	position: absolute;
	top: 15%;
	font-size: 42px;
	color: #fff;
}

.basicDesc {
	margin-left: 200px;
	display: flex;
	flex-direction: column;
	gap: 6px;
}

.basicDesc > span {
	color: var(--title-color);
	font-size: 42px;
	font-weight: 500;
}

#title {
	font-size: 60px;
	font-weight: 500;
}

.hobbyContainer {
	margin-top: 100px;
	margin-left: 200px;
	height: auto;
}

.hobbyContainer > span {
	color: rgba(0, 0, 0, 0.55);
	font-size: 36px;
	font-weight: 500;
}

.hobbyGroup {
	display: flex;
	width: 80%;
	flex-direction: row;
	justify-content: space-between;
	padding-top: 40px;
	margin-top: 60px;
}

.hobbyGroup > div {
	height: 400px;
	/* border: 1px solid #fff; */
	width: 30%;
	border-radius: 10px;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	/* box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
		0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12); */
	box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.3),
		0 1px 3px 1px rgba(60, 64, 67, 0.15);
}

.hobbyGroup > div:hover .guitarHobbyPic {
	height: 65%;
}

.hobbyGroup > div:hover .hobbyDesc {
	height: 35%;
}

.guitarHobbyPic {
	background-color: #85a2e5;
	width: 100%;
	height: 75%;
	background-image: url('../../img/guitar.jpg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	transition: height 0.6s;
}
/* ------设置其他两个爱好的图片------ */
.goHobbyPic {
	background-image: url('../../img/go.jpeg');
	background-color: #85a2e5;
	width: 100%;
	height: 75%;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	transition: height 0.6s;
}

.washPaintingHobbyPic {
	background-image: url('../../img/washPainting.jpg');
	background-color: #85a2e5;
	width: 100%;
	height: 75%;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	transition: height 0.6s;
}
/* ------ end ------ */
.hobbyDesc {
	background-color: transparent;
	width: 100%;
	height: 25%;
	display: flex;
	flex-direction: column;
	align-items: center;
	transition: height 0.6s;
}

.title {
	margin-top: 20px;
	font-size: 24px;
	font-weight: 500;
	margin-bottom: 1px;
	color: rgba(0, 0, 0, 0.87);
}

.desc {
	text-align: center;
	font-size: 18px;
	color: rgba(0, 0, 0, 0.55);
}

.underlineEffect {
	background: linear-gradient(
			to right,
			rgba(60, 64, 67, 0.45),
			rgba(60, 64, 68, 0.45)
		)
		no-repeat right bottom;
	cursor: pointer;
	background-size: 0 2px;
	transition: background-size 0.6s;
}

.underlineEffect:hover {
	background-position-x: left;
	background-size: 100% 2px;
}

.contact {
	margin-top: 200px;
	margin-bottom: 200px;
}

.contact {
	display: flex;
	flex-direction: column;
	margin-left: 200px;
}

.contactTitle {
	color: var(--title-color);
	font-size: 42px;
	font-weight: 500;
	opacity: 0.55;
	margin-bottom: 100px;
}

.contactSlot {
	margin-top: 50px;
	margin-left: 20px;
}
