.iconContainer {
	height: 400px;
	width: 80%;
	/* background-color: aqua; */
	position: relative;
}

.contactSlot {
	margin-top: 50px;
	margin-left: 20px;
}

.iconGroup {
	display: flex;
	flex-direction: row;
	gap: 20px;
	position: absolute;
	right: 0;
	bottom: 0;
	margin-right: 80px;
	margin-bottom: 40px;
}

.iconGroup::after {
	content: '';
	position: absolute;
	bottom: -5px;
	left: -80px;
	width: 130%;
	height: 3px;
	background-color: #000;
	opacity: 0.4;
}

.iconGroup > div > img {
	width: 40px;
	height: 40px;
	opacity: 0.2;
	transition: opacity 0.6s ease-in-out;
}

.iconGroup > div > img:hover {
	opacity: 0.7;
}
